
.catalog {
  display: flex;
  flex-wrap: wrap;

  justify-content: left;
  height: fit-content;
  width: auto;

  margin-top: 1.3vh;
  margin-left: -1px;
  margin-bottom: 4vh;
  position: relative;
  /* z-index: 7; */
}

.catalog .shop-item {
  flex: 1 1 230px;
  margin-top: 1px;
  margin-left: 1px;
  justify-content: center;
  text-align: center;
  align-self: center;
  padding: 1px;
  font-family: Arial, Helvetica, sans-serif;
  max-width: 240px;
  min-width: 200px;


}

